.list {
    list-style: none;
    padding: 0;
    font-weight: 500;
    font-size: 18px;
  }
  .contact {
    display: flex;
    align-items: center;
  }
  .contact p {
    margin-right: 12px;
  }
  .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
    height: 30px;
    width: 80px;
  }
  
  .btn:hover,
  .btn:focus {
    color: aliceblue;
    background-color: rgb(90, 134, 231);
  }
  
  .btn span {
    margin-right: 5px;
  }